<template>
  <div class="AcceptRegist">
    <div class="head">登记机关注册使用账号</div>
    <div class="body">
      <RadioGroup v-model="choose" @on-change="chooseRole" style="margin-left:100px;margin-bottom:10px;">
        <Radio label="acceptor">受理机关</Radio>
        <Radio label="approver">审批机关</Radio>

      </RadioGroup>
      <Form :model="registInfo" :label-width="120" :rules="rules" ref="registInfo">
        <Row>

          <FormItem prop="username" label="用户名">
            <Poptip trigger="focus" placement="right">
              <div slot="content" style="white-space: normal;font-size:2px;">
                <p>登录账号，至少5个字符，要求大小写字母和数字</p>
              </div>
              <Input v-model="registInfo.username" placeholder="用户名"></Input>
            </Poptip>

          </FormItem>

        </Row>
        <!--<label class="label">本人姓名：</label>-->

        <!--<label class="label">密码：</label>-->
        <Row>

          <FormItem prop="password" label="密码">
            <Poptip trigger="focus" placement="right">
              <div slot="content" style="white-space: normal;font-size:2px;">
                <p>登录密码，至少5个字符，要求大小写字母和数字</p>
              </div>
              <Input type="password" v-model="registInfo.password" placeholder="密码"></Input>
            </Poptip>

          </FormItem>

        </Row>
        <Row>

          <FormItem prop="password2" label="确认密码">
            <Poptip trigger="focus" placement="right">
              <div slot="content" style="white-space: normal;font-size:2px;">
                <p>确认两次密码填写相等</p>
              </div>
              <Input type="password" v-model="registInfo.password2" placeholder="确认密码"></Input>
            </Poptip>

          </FormItem>

        </Row>




          <Row>
            <FormItem prop="useComAddr" label="登记机关名称"  style="margin-bottom:10px;"
                      class="ivu-form-item-required ivu-form-item-label">
            <Col span="7" style="margin-right:20px;">
            <Select v-model="province" filterable @on-change="chosenPro"
                    style="width:120%;">
              <Option v-for="item in provinceList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            </Col>
            <Col span="7" style="margin-right:20px;">
            <Select v-model="city" filterable @on-change="chosenCity" style="width:120%;">
              <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            </Col>

            <Col span="7">
            <Select v-model="area" filterable @on-change="chosenArea" style="width:120%;">
              <Option v-for="item in areaList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            </Col>
            </FormItem>

          </Row>

        <FormItem prop="acceptCom" label="">
          <Row>
            <Select v-model="registInfo.acceptCom" filterable @on-change="chosenAccept" :label-in-value="true">
              <Option v-for="item in acceptComList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </Row>
        </FormItem>

        <Row>
          <FormItem prop="name" label="登记人员">

            <Input v-model="registInfo.name" placeholder="填写负责登记工作的人员姓名"></Input>

          </FormItem>
        </Row>


        <Row>
          <FormItem prop="verifyId" label="身份证号">

            <Input v-model="registInfo.verifyId" placeholder="请填入登记人员的公民身份证号码"></Input>

          </FormItem>
        </Row>


        <FormItem prop="mobilePhone" label="移动电话号码">
          <Row>
            <Input v-model="registInfo.mobilePhone" placeholder="请填入安全管理员的移动电话号码"></Input>
          </Row>
        </FormItem>


        <Row>

          <Button type="primary" @click="regist()" long style="font-size:16px;font-weight:bold;">立即注册</Button>


        </Row>

      </Form>
    </div>
  </div>
</template>
<script>


  import {mapGetters, mapState, mapActions} from 'vuex'
  import * as setAppService from '../../services/setApp'
  import * as loginService from '../../services/login'
  import qs from 'qs'


  export default {
    data() {
      return {
        choose: "acceptor",
        role: 'acceptor',
        registInfo: {
          username: '',
          province: '',
          city: '',
          area: '',
          acceptCom: '',
          name: '',
          verifyId: '',
          password: '',
          password2: '',
          useComName: '',
          mobilePhone: '',
        },
        acceptComList: [],


        province: '',
        provinceId: '',
        city: '',
        provinceList: [],
        cityList: [],
        cityId: '',
        area: '',
        areaList: [],
        areaId: '',
        acceptCom: '',
        verifyId: '',


        rules: {
          useComName: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],
//          acceptCom: [
//            {required: true, message: '不能为空', trigger: 'blur'}
//          ],
          name: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],
          verifyId: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],
          mobilePhone: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],
          password2: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],
          username: [
            {required: true, message: '不能为空', trigger: 'blur'}
          ],

        },


      }
    },


    computed: {
      defaultActive: function () {
        return this.$route.path.replace('/', '');
      },

      ...mapGetters([
        "headShow",


      ])
    },

    mounted() {
      this.author_key = localStorage.getItem('author_key');
      this.initData();

    },
//    activated() {
//      const _this = this;
//      _this.initData();
//    },
    components: {},
    methods: {
      chooseRole(value){
        console.log(value)
        if (value == '') {
          this.role = 'acceptor��������';
        } else {
          this.role = 'approver��������';

        }

      },
      initData(){
        setAppService.getProvinces().then(res => {
          for (let i = 0, len = res.length; i < len; i++) {
            this.provinceList.push({value: res[i].code, label: res[i].name});
          }
          console.log(this.provinceList);
        }).catch(error => {
          console.log(error);
        })
      },
      chosenPro(value){
        let params = 'provinceCode=' + value;

        this.provinceId = value;
        if (value !== '') {
          setAppService.getCities(params).then(res => {
            this.cityList = [];
            for (let i = 0, len = res.length; i < len; i++) {
              this.cityList.push({value: res[i].code, label: res[i].name});
            }
          }).catch(error => {
            console.log(error);
          })
        }


      },
      chosenCity(value){
        let params = 'cityCode=' + value;
        let params2 = 'code=' + value;
        this.cityId = value;
        if (value !== "") {
          setAppService.getArea(params).then(res => {
            this.areaList = [];
            for (let i = 0, len = res.length; i < len; i++) {
              this.areaList.push({value: res[i].code, label: res[i].name});
            }
          }).catch(error => {
            console.log(error);
          })

          loginService.getAccpeter(params2).then(res => {
            for (let i = 0, len = res.length; i < len; i++) {
              this.acceptComList.push({value: res[i].id, label: res[i].name});
            }
          }).catch(error => {
            console.log(error);
          })
        }
      },
      chosenArea(value){
        this.areaId = value;
        let params = 'addressCode=' + value;

        loginService.getAccpeter(params).then(res => {
          this.acceptComList = [];
          for (let i = 0, len = res.length; i < len; i++) {
            this.acceptComList.push({value: res[i].id, label: res[i].name});
          }
        }).catch(error => {
          console.log(error);
        })


      },
      chosenAccept(value){
        console.log(value);
        this.propertyComCode = value.value;

        this.propertyComName =value.label;
      },
      regist(){
        this.$refs['registInfo'].validate((valid) => {
          if (valid) {
            if (this.registInfo.password !== this.registInfo.password2) {
              this.$Notice.error({
                title: '这是通知标题',
                desc: '两次密码填写不相同'
              });
              return
            }
            if(this.registInfo.acceptCom==''){
              this.$Notice.error({
                title: '这是通知标题',
                desc: '请选择登记机关'
              });
              return
            }
            let params = {};
            params.username = this.registInfo.username;
            params.password = this.registInfo.password;
            let temp = {
              "name": this.registInfo.name,
              "acceptorAgencyId": parseInt(this.propertyComCode),
              "acceptorAgencyName": this.propertyComName,
              "verifyId": this.registInfo.verifyId,
              "mobilePhone": this.registInfo.mobilePhone,
            }
            params.userData = temp;
            console.log(this.role);

//        params=qs.stringify(params);
            if (this.role == 'acceptor') {
              params.userData.role = 2;

              loginService.RegistAcceptor(params).then(res => {
                if (res.status === 200) {
                  this.$Message.info('注册成功，请登录');
                  this.$router.push('login');
                } else if (res.status === 500) {
                  this.$Message.info(res.msg);
                }

              }).catch(error => {
                // console.log(2);
                console.log(error)
              })
            } else {
              params.userData.role = 3;
              loginService.RegistApprover(params).then(res => {
                if (res.status === 200) {
                  this.$Message.info('注册成功，请登录');
                  this.$router.push('login');
                } else if (res.status === 500) {
                  this.$Message.info(res.msg);
                }

              }).catch(error => {
                // console.log(2);
                console.log(error)
              })
            }

          } else {
            console.log('error submit!!');
            this.$Message.info('尚有信息不符合要求，请检查');
            return false;
          }
        });


      }
    }
  }


</script>
<style lang="scss" scoped>
  .AcceptRegist {
    width: 60%;
    margin: 0 auto;
    margin-top: 50px;
    padding-bottom: 10px;
    height: 80%;
    border: 2px solid #dddee1;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-bottom-right-radius: 3px;
    border-bottom-left-radius: 3px;
  }

  .head {
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -webkit-justify-content: center;
    justify-content: center;
    font-weight: bold;
    font: #464c5b;
    font-size: 30px;
  }

  .body {
    width: 50%;
    margin: 0 auto;
    padding: 20px 20px 20px 20px;
  }

  .label {
    text-align: right;
    vertical-align: middle;
    float: left;
    font-size: 16px;

    line-height: 1;
    padding: 10px 12px 10px 0;
    box-sizing: border-box;

  }


</style>



